import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";

/* 영화진흥 위원회 오픈 api 발급 키 */
const API_KEY = "d819c901839cc7b4f6745a60307426d4";
const API =
  "http://www.kobis.or.kr/kobisopenapi/webservice/rest/boxoffice/searchDailyBoxOfficeList.json";

/* 비동기 처리 함수 구현 */
export const getMovieRank = createAsyncThunk(
  "MovieRankSlice/getMovieRank",
  async (payload, { rejectWidthValue }) => {
    let result = null;

    try {
      result = await axios.get(API, {
        params: {
          key: API_KEY,
          //  payload는 이 함수를 호출할 때 전달되는 파라미터
          /* 컨트롤러에서 전달하는 파라미터는 payload로 전달된다.
            --> 단일 값인 경우 payload 자체가 그 값
            --> JSON인 경우 payload가 JSON이 된다.
            */
          targetDt: payload.targetDt,
        },
      });
    } catch (err) {
      // 에러 발생 시 'rejectWidthValue()'함수에 에러 데이터를 전달하면 extraReducer의 rejected 함수가 호출된다.
      result = rejectWidthValue(err.response);
    }
    return result;
  }
);

/* Slice 정의 (Action함수 + Reducer의 개념) */
const MoviRankSlice = createSlice({
  name: "movieRank",
  initialState: {
    data: null,
    loading: false,
    error: null,
  },
  // 내부 action 밒 동기 action
  reducers: {},
  // 외부 action 및 비동기 (Ajax용)
  extraReducers: {
    [getMovieRank.pending]: (state, { payload }) => {
      return { ...state, loading: true };
    },
    [getMovieRank.fulfilled]: (state, { payload }) => {
      return {
        data: payload?.data,
        loading: false,
        error: null,
      };
    },
    [getMovieRank.rejected]: (state, { payload }) => {
      return {
        data: payload?.data,
        loading: false,
        error: {
          code: payload?.status ? payload.status : 500,
          message: payload?.statusText ? payload.statusText : "Server Error",
        },
      };
    },
  },
});
// 리듀서 객체 내보내기
export default MoviRankSlice.reducer;
